body {
    font-family: 'PT Sans', sans-serif;
    font-size: 15px;
    background-color: #eee;
    text-align: center;
}

.modal-opened {
    overflow: hidden;
}

.modal-overlay {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 100;
    background-color: rgba(0, 0, 0, 0.5);
    opacity: 0;
    -webkit-transition: opacity 0.25s;
    -o-transition: opacity 0.25s;
    transition: opacity 0.25s;
    overflow: auto;
    text-align: left;
}

.modal-content {
    left: 50%;
    -webkit-transform: translate(-50%, -20%);
        -ms-transform: translate(-50%, -20%);
            transform: translate(-50%, -20%);
    max-width: 600px;
    -webkit-transition: -webkit-transform 0.25s;
    transition: -webkit-transform 0.25s;
    -o-transition: transform 0.25s;
    transition: transform 0.25s;
    transition: transform 0.25s, -webkit-transform 0.25s;
    overflow: hidden;
    margin: 30px 0;
}

.modal-header {
    color: #fff;
    background: -webkit-gradient(linear, left top, left bottom, from(#1aa4d8), to(#1a97cb));
    background: -webkit-linear-gradient(#1aa4d8, #1a97cb);
    background: -o-linear-gradient(#1aa4d8, #1a97cb);
    background: linear-gradient(#1aa4d8, #1a97cb);
    border-bottom: 1px solid rgba(0, 0, 0, 0.2);
    font-size: 18px;
}

.modal-footer {
    background-color: #f6f6f6;
}

.ReactModal__Overlay--after-open {
    opacity: 1;
}

.ReactModal__Overlay--before-close {
    opacity: 0;
}

.ReactModal__Content--after-open {
    -webkit-transform: translate(-50%, 0);
        -ms-transform: translate(-50%, 0);
            transform: translate(-50%, 0);
}

.ReactModal__Content--before-close {
    -webkit-transform: translate(-50%, -20%);
        -ms-transform: translate(-50%, -20%);
            transform: translate(-50%, -20%);
}

textarea.form-control {
    resize: vertical;
    min-height: 95px;
}

textarea.form-control.small {
    min-height: 55px;
}

textarea.form-control.large {
    min-height: 150px;
}

.buttons {
    text-align: right;
}

.footer {
    margin-bottom: 50px;
}



